import { useState, useEffect } from "react";
import Link from "next/link";
import { Menu, X, Sun, Moon } from "lucide-react";
import { useRouter } from "next/router";

export default function Layout({ children }) {
  const [menuOpen, setMenuOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);
  const [darkMode, setDarkMode] = useState(false);
  const router = useRouter();

  // ✅ Handle scroll shadow effect
  useEffect(() => {
    const handleScroll = () => setScrolled(window.scrollY > 10);
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  // ✅ Load dark mode preference
  useEffect(() => {
    const stored = localStorage.getItem("khedme-theme");
    if (stored === "dark") setDarkMode(true);
  }, []);

  // ✅ Apply dark mode class to document
  useEffect(() => {
    if (darkMode) {
      document.documentElement.classList.add("dark");
      localStorage.setItem("khedme-theme", "dark");
    } else {
      document.documentElement.classList.remove("dark");
      localStorage.setItem("khedme-theme", "light");
    }
  }, [darkMode]);

  const navLinks = [
    { href: "/", label: "Home" },
    { href: "/jobs", label: "Jobs" },
    { href: "/provider", label: "Provider" },
    { href: "/admin/approve", label: "Admin" },
  ];

  return (
    <div className="flex flex-col min-h-screen bg-gradient-to-b from-orange-50 to-white dark:from-gray-900 dark:to-gray-950 text-gray-800 dark:text-gray-100 transition-colors duration-300">
      {/* ✅ Header */}
      <header
        className={`fixed top-0 w-full z-50 transition-all duration-300 ${
          scrolled
            ? "bg-white/80 dark:bg-gray-900/80 backdrop-blur-md shadow-md py-2"
            : "bg-white/60 dark:bg-gray-900/60 backdrop-blur-sm py-4"
        }`}
      >
        <div className="max-w-6xl mx-auto px-6 flex justify-between items-center">
          {/* ✅ Logo */}
          <Link
            href="/"
            className="text-2xl font-extrabold tracking-tight text-gray-900 dark:text-white"
          >
            <span className="text-orange-500">Khedme</span>
            <span className="ml-1">بال</span>
          </Link>

          {/* ✅ Desktop Nav */}
          <nav className="hidden md:flex space-x-8 text-gray-700 dark:text-gray-200 font-medium">
            {navLinks.map(({ href, label }) => (
              <Link
                key={href}
                href={href}
                className={`relative transition ${
                  router.pathname === href
                    ? "text-orange-600 dark:text-orange-400 font-semibold"
                    : "hover:text-orange-500 dark:hover:text-orange-400"
                }`}
              >
                {label}
                {router.pathname === href && (
                  <span className="absolute -bottom-1 left-0 w-full h-[2px] bg-orange-500 rounded-full" />
                )}
              </Link>
            ))}
          </nav>

          <div className="flex items-center space-x-3">
            {/* ✅ Theme Toggle */}
            <button
              onClick={() => setDarkMode(!darkMode)}
              className="p-2 rounded-full hover:bg-orange-100 dark:hover:bg-gray-800 transition"
              title={darkMode ? "Switch to Light Mode" : "Switch to Dark Mode"}
            >
              {darkMode ? (
                <Sun size={20} className="text-orange-400" />
              ) : (
                <Moon size={20} className="text-orange-500" />
              )}
            </button>

            {/* ✅ “Post Job” (Desktop only on /jobs) */}
            {router.pathname === "/jobs" && (
              <button
                onClick={() => router.push("/post-job")}
                className="hidden md:block bg-orange-500 hover:bg-orange-600 text-white text-sm font-medium px-4 py-2 rounded-full shadow-md transition"
              >
                Post a Job
              </button>
            )}

            {/* ✅ Mobile menu toggle */}
            <button
              onClick={() => setMenuOpen(!menuOpen)}
              className="md:hidden text-gray-700 dark:text-gray-200"
            >
              {menuOpen ? <X size={26} /> : <Menu size={26} />}
            </button>
          </div>
        </div>

        {/* ✅ Mobile Menu */}
        {menuOpen && (
          <div className="md:hidden bg-white dark:bg-gray-900 border-t border-orange-100 dark:border-gray-800 text-center">
            {navLinks.map(({ href, label }) => (
              <Link
                key={href}
                href={href}
                onClick={() => setMenuOpen(false)}
                className={`block py-3 transition ${
                  router.pathname === href
                    ? "bg-orange-50 dark:bg-gray-800 text-orange-600 dark:text-orange-400 font-semibold"
                    : "text-gray-700 dark:text-gray-200 hover:bg-orange-50 dark:hover:bg-gray-800 hover:text-orange-600 dark:hover:text-orange-400"
                }`}
              >
                {label}
              </Link>
            ))}

            {router.pathname === "/jobs" && (
              <button
                onClick={() => {
                  setMenuOpen(false);
                  router.push("/post-job");
                }}
                className="w-11/12 mx-auto my-3 bg-orange-500 hover:bg-orange-600 text-white text-sm font-medium px-4 py-2 rounded-full shadow-md transition"
              >
                Post a Job
              </button>
            )}
          </div>
        )}
      </header>

      {/* ✅ Main content */}
      <main className="flex-grow pt-24 px-4 transition-colors duration-300">
        {children}
      </main>

      {/* ✅ Footer */}
      <footer className="text-center py-6 text-sm text-gray-500 dark:text-gray-400 border-t border-gray-200 dark:border-gray-800 transition-colors duration-300">
        © {new Date().getFullYear()}{" "}
        <span className="text-orange-500 font-semibold">Khedmeبال</span>. All
        rights reserved.
      </footer>
    </div>
  );
}
